import { cn } from '@/lib/utils';
import Link from 'next/link'
import React from 'react'
import { FaShoppingCart } from "react-icons/fa";
import Image from 'next/image';

interface Props{
    mobile?: boolean;
    loggedInUser?:{
        name: string;
        email: string;
        image: string;
    };
    logout?: () => Promise<void>;
}

const NavItems = ({mobile, loggedInUser, logout}: Props) => {
  return (
    <div className={cn('flex items-center justify-center gap-6', mobile ? 'flex-col' : 'flex-row')}>
        { loggedInUser ?
        <>
        <div className='w-[50px] h-[50px] rounded-full overflow-hidden shadow-md relative'>
        {/* Profile picture container */}
        <Image src={loggedInUser.image} alt='profile pic' className='object-cover' fill  />
        </div>

        <Link
            href='/profile'
            className='text-lg font-medium text-gray-900 hover:text-gray-700 transition'
        >
            {loggedInUser.name || 'User'}
        </Link>

        {logout ? (
            <form action={logout}>
                <button type='submit' className='nav-btn'>Logout</button>
            </form>
        ) : null}
        </>

        :

        <Link href='/signin' className='nav-btn'>Login</Link>
        }
        

        <div className='relative flex items-center justify-center h-[60px] w-[60px] cursor-pointer'>
            <FaShoppingCart className='text-4xl' />
            <span className='absolute top-0 right-0 px-2 py-1 bg-black rounded-full text-white'>
                3
            </span>
        </div>
    </div>
  )
}

export default NavItems